.navbar {
  position: fixed;
  z-index: 4;
  background-color: rgba(101, 19, 19, 0.95);
  padding: 4px 16px;
  width: 100%;
  display: flex;
  align-items: center;
  float: left;
  box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.75);
  transition: background-color 0.1s ease-in-out;

  &:hover {
    background: rgba(101, 19, 19, 1);
  }
  .navbar-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 64px;
    }
  }
}

.navbar-nav {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  font-size: 1.75em;
  .nav-item {
    display: flex;
    flex: 1;
    justify-content: center;

    .navbar-mobile-section {
      display: none;
    }
    &:nth-child(2) {
      flex: 2;
    }
    &:not(:last-child) {
      border-right: 1px solid #ffffff;
    }
    &.nav-item--flex {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;
    }
    a {
      text-decoration: none;
      text-align: center;
      color: #ffffff;
      outline: none;
      padding: 12px;
      border-radius: 8px;
      transition: background-color 0.2s ease-in-out;
      font-size: 24px;

      &:hover,
      &:focus {
        background-color: #dddddd30;
      }
    }
  }
}

.navbar-mobile {
  display: none;
}

.menu-close__icon {
  display: none;
}
// footer
.banner-footer {
  display: flex;
  justify-content: flex-end;
  background-color: rgba(88, 17, 17, 1);
  color: #ffffff;
  font-family: "Petrona Regular";
  padding: 1.5em 1em;
}

.language-selector {
  position: relative;

  .display {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(white, 0.16);
    border-radius: 8px;
    padding: 4px 12px;
    cursor: default;

    max-width: 240px;
    color: white;
    p {
      padding: 4px 8px;
      font-size: 22px;
    }

    .language_icon {
      height: 26px;
      padding: 2px 8px;
      color: white;
    }

    .arrow-down {
      margin-top: 4px;
      padding: 2px 8px;
    }
  }

  .options {
    width: 100%;
    background: #651313;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 8px;
    height: 0;
    overflow: hidden;
    transition: height 0.3s ease-in-out;

    a {
      cursor: pointer;
    }
  }
  &:hover,
  &:focus-within {
    .options {
      height: 160px;
    }
  }
}
@media only screen and (max-width: 768px) {
  header {
    position: fixed;
    width: 100%;
  }

  .navbar {
    position: fixed;
    top: 0;
    left: -90%;
    width: 80%;
    height: 100vh;
    padding: 16px;
    display: block;
    transition: left 1s;
    background-color: rgba(101, 19, 19, 1);
    box-shadow: 0 0 8px 4px rgba(0, 0, 0, 0.3);
    .navbar-nav {
      margin-left: 0px;
      margin-top: 32px;
      display: block;

      .nav-item {
        width: 100%;
        padding: 16px 0;
        border-right: 0;

        .navbar-mobile-section {
          display: inherit;
          font-size: 26px;
          color: #ffffff;
          margin: 4px 8px 8px 8px;
          padding:  4px 8px 8px 8px;
          font-weight: bold;
        }

        &:not(:first-child) {
          border-top: 1px solid #ffffff;
        }
        &.nav-item--flex {
          flex-direction: column;

        }
      }
      // &:last-child {
      //   border-bottom: 1px solid #ffffff;
      // }
    }
  }

  .language-selector {
    .display {
      display: none;
    }
    .options {
      height: auto;
      position: inherit;

      &:hover {
        height: auto;
      }
    }
  }

  .navbar-mobile {
    background-color: rgba(101, 19, 19, 1);
    padding: 12px;
    display: flex;
    height: 100%;
    .nav-item {
      text-align: left;
    }
  }

  .menu-close__icon {
    display: block;
    position: absolute;
    right: 12px;
    top: 12px;
  }
}

@media only screen and (min-width: 601px) and (max-width: 768px) {
  .navbar {
    width: 40%;
  }
}
